import React, { useEffect, useState } from 'react';
import cc from 'classcat';
import { IconType } from './IconImg.types';
import './IconImg.css';

export interface IconProps extends React.HTMLAttributes<HTMLSpanElement> {
  icon: IconType;
  size?: 'sm' | 'md' | 'lg';
}

export const IconImg = ({ icon, size = 'md', ...restProps }: IconProps) => {
  return (
    <span className={cc(['Icon_root', `Icon--${size}`])} {...restProps}>
      <img src={`album.svg`} alt="" />
      <img src={`/album.svg`} alt="" />
      <img src={`icons/album.svg`} alt="" />
      <img src={`/icons/album.svg`} alt="" />
      <img
        src={`https://raw.githubusercontent.com/oriyan-ovadia/poc-lucide-icons/main/${icon}.svg`}
        alt=""
      />
    </span>
  );
};
